@keyframes heart-beat {
    0% {
        transform: scale(1);
    }
    25% {
        transform: scale(2);
    }
    50% {
        transform: scale(3);
    }
    75% {
        transform: scale(2);
    }
    100% {
        transform: scale(1);
    }
}

.nav-bar {
    display: flex;
    justify-content: space-around;
    list-style: none;
    font-weight: bold;
    background-color: white;
    padding: 2%;
}

.home-body {
    margin-top: -3%;
    text-align: center;
    background-repeat: no-repeat;
    width: 100vw;
    height: 80vh;
    background-image: url("https://images.unsplash.com/photo-1487700160041-babef9c3cb55?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1035&q=80");
    background-size: cover;
}

.home-body>h1 {
    position: relative;
    top: 25%;
    font-size: 4.5rem;
}

.home-body>h2 {
    position: relative;
    top: 20%;
}

.home-body>h1:hover {
    color: rgb(255, 0, 255);
    transform: matrix(1.5, 0, 0, 1, 100, 50);
}

.contact-style {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

#f:hover {
    color: blue;
    animation-name: heart-beat;
    animation-duration: 5s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
}

#l:hover {
    color: rgb(7, 7, 107);
    animation-name: heart-beat;
    animation-duration: 5s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
}

#I:hover {
    color: magenta;
    animation-name: heart-beat;
    animation-duration: 5s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
}

#G:hover {
    color: rgb(255, 4, 4);
    transform: scale(3);
    animation-name: heart-beat;
    animation-duration: 5s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
}

#W:hover {
    color: green;
    animation-name: heart-beat;
    animation-duration: 5s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
}

.contact {
    background-image: url("https://images.unsplash.com/photo-1509395062183-67c5ad6faff9?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=800&q=80");
    background-size: cover;
    height: 80vh;
}

.resume-body {
    width: 100vw;
    background-color: rgb(247, 242, 242);
    overflow: hidden;
}

div [class^=container] {
    position: relative;
    text-align: start;
    padding: 2%;
    top: 50px;
    bottom: 20px;
    background-color: rgb(255, 255, 255);
    width: 60%;
    margin: 2% auto;
    height: auto;
    box-shadow: 0 2px rgb(218, 214, 208);
}

span {
    font-weight: bold;
}

li {
    color: blue;
}

progress::-webkit-progress-value {
    background: pink;
}